<template>
  <div class="game-options">
    <h2>Game Options:</h2>
    <input @change="changeMode" id="normal" type="radio" name="mode" value="normal" checked />
    <label for="normal">Normal</label>
    <br />
    <input @change="changeMode" id="sound-only" type="radio" name="mode" value="sound-only" />
    <label for="sound-only">Sound Only</label>
    <br />
    <input @change="changeMode" id="light-only" type="radio" name="mode" value="light-only" />
    <label for="light-only">Light Only</label>
    <br />
    <input @change="changeMode" id="free-board" type="radio" name="mode" value="free-board" />
    <label for="free-board">Free board</label>
  </div>
</template>

<script>
import store from '../store/index';

export default {
  methods: {
    changeMode: function(e) {
      store.commit('mode', e.target.value);
    }
  },
  computed: {
    mode: {
      get() {
        return store.getters.mode;
      },
    },
  },
};
</script>

<style scoped>
.game-options h2 {
  margin-top: 30px;
  margin-bottom: 0;
}

.game-options input[type="radio"] {
  margin-right: 10px;
}
</style>