import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    sequence: [],
    copy: [],
    round: 0,
    active: true,
    mode: "normal",
    tile: 0,
    activateSimonBoard: false,
  },
  getters: {
    mode: (state) => {
      return state.mode;
    },
    sequence: (state) => {
      return state.sequence;
    },
    tile: (state) => {
      return state.tile;
    },
    active: (state) => {
      return state.active;
    },
    copy: (state) => {
      return state.copy;
    },
    round: (state) => {
      return state.round;
    },
    activateSimonBoard: (state) => {
      return state.activateSimonBoard;
    },
  },
  mutations: {
    mode(state, mode) {
      state.mode = `${mode}`;
    },
    newRound(state) {
      ++state.round;
      state.sequence.push(Math.floor(Math.random() * 4 + 1));
      state.copy = state.sequence.slice(0);
    },
    activateSimonBoard(state) {
      state.activateSimonBoard = true;
    },
    deactivateSimonBoard(state) {
      state.activateSimonBoard = false;
    },
    playSound(state, tile) {
      let sound = document.querySelector("#sound");
      let sound1 = require.context("../sounds/", false, /\.ogg$/);
      let sound2 = require.context("../sounds/", false, /\.mp3$/);

      sound.innerHTML = `
        <audio id="audio" autoplay>
          <source src="${sound1("./" + tile + ".ogg")}" type="audio/ogg">
          <source src="${sound2("./" + tile + ".mp3")}" type="audio/mp3">
        </audio>
        `;
    },
    lightUp(state, tile) {
      let tileElem = document.querySelector(`[data-tile="${tile}"]`);
      tileElem.classList.add("active");
      setTimeout(() => {
        tileElem.classList.remove("active");
      }, 250);
    },
  },
  actions: {
    startRound(context) {
      context.state.sequence = [];
      context.state.copy = [];
      context.state.round = 0;
      context.dispatch("newRound");
    },
    newRound(context) {
      context.commit("newRound");
      context.dispatch("animate", context.getters.sequence);
    },
    registerClick(context, tile) {
      let desiredResponse = context.state.copy.shift();
      let actualResponse = parseInt(tile);
      context.state.active = desiredResponse === actualResponse;

      context.dispatch("checkLose");
    },
    checkLose(context) {
      if (context.state.copy.length === 0 && context.state.active) {
        context.commit("deactivateSimonBoard");
        context.dispatch("newRound");
      } else if (!context.state.active) {
        context.commit("deactivateSimonBoard");
        context.dispatch("endGame");
      }
    },
    endGame(context) {
      if (!context.state.active) {
        let loseElem = document.querySelector('[data-action="lose"]');
        loseElem.style.display = "block";
      }
    },
    animate(context, sequence) {
      var i = 0;
      var interval = setInterval(function() {
        context.getters.mode !== "light-only"
          ? context.commit("playSound", sequence[i])
          : null;
        context.getters.mode !== "sound-only"
          ? context.commit("lightUp", sequence[i])
          : null;

        i++;
        if (i >= sequence.length) {
          clearInterval(interval);
          context.commit("activateSimonBoard");
        }
      }, 600);
    },
  },
});
